.header {
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    /* padding: 40px 0; */
    background-color: var(--dark-bg);
    background-image: url('./../../img/header-bg.png');
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center center;
    color: var(--header-text);
    flex: 1;
}

.header__wrapper {
    padding: 0 15px;
    max-width: 660px;
}

.header__title {
    margin-bottom: 20px;
    font-size: 40px;
    font-weight: 700;
    line-height: 1.4;
}

.header__title strong {
    font-size: 60px;
    font-weight: 700;
}

.header__title em {
    font-style: normal;
    color: var(--accent);
}

.header__text {
    margin-bottom: 40px;
    font-size: 18px;
    line-height: 1.333;
}

.header__text p + p {
    margin-top: 1em;
}

@media (max-width: 620px) {
    .header__title {
        font-size: 30px;
    }
    .header__title strong {
        font-size: 40px;
    }
}