@import "./reset.css";
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap');

/* font-family: 'DM Sans', sans-serif;
font-family: 'Poppins', sans-serif; */

:root {
    --blackpure: #000;
    --black: #171718;
    --black-border: #26292D;
    --white: #fff;
    --purple: #5C62EC;
    --orange: #f3a62b;

    --text-color: var(--black);
    --dark-bg: var(--black);
    --dark-border: var(--black-border);
    --header-text: var(--white);
    --accent: var(--orange);

    /* Светлая тема */
    --page-bg: var(--white);
    --text-color: var(--black);
    --title-1: var(--accent);
    --poject-card-bg: var(--white);
    --project-card-text: var(--black);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}

 /* Темная тема */

.dark {
    /* --page-bg: #252526; */
    /* --text-color: var(--white); */
    --title-1: var(--white);
    --poject-card-bg: var(--black);
    --project-card-text: var(--white);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.8);
    --accent: var(--purple);
    --title-1: var(--accent);
}

html, body {
    height: 100%;
    font-family: 'DM Sans', sans-serif;
    letter-spacing: -0.5px;
    background-color: var(--page-bg);
    color: var(--text-color);
}

#root {
    height: 100%;
}

.App {
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

/* Общие стили */

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}

.none {
    display: none !important;
}

.title-1 {
    margin-bottom: 60px;
    font-size: 60px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--title-1);
    text-align: center;
}

.title-2 {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 40px;
    line-height: 1.3;
}

.section {
    /* height: 100%; */
    padding: 70px 0;
}

/* Кнопка */

.btn {
    display: inline-block;
    height: 48px;
    padding: 12px 28px;
    border-radius: 5px;
    background-color: var(--accent);
    color: var(--white);
    letter-spacing: 0.15px;
    font-size: 16px;
    font-weight: 500;
    transition: opacity 0.2s ease-in;
}

.btn:hover {
    opacity: 0.7;
}

.btn:active {
    position: relative;
    top: 1px;
}

/* Проекты */

.projects {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 30px;
}

/* Страница проекта */

.project-details {
    margin: 0 auto;
    max-width: 865px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.project-details__cover {
    max-width: 100%;
    margin-bottom: 40px;
    box-shadow: var(--box-shadow);
    border-radius: 10px;
}

.project-details__desc {
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.3;
}

/* Контент лист */

.content-list {
    margin: 0 auto;
    max-width: 570px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    row-gap: 40px;
}

.content-list a {
    color: var(--accent);
}

.content-list__item {
    font-size: 18px;
    line-height: 1.5;
}

.content-list__item p+p {
    margin-top: 0.5em;
}

@media (max-width: 620px) {
    
    .section {
        padding: 40px 0;
    }
    .title-1 {
        margin-bottom: 30px;
        font-size: 40px;
    }
    .title-2 {
        margin-bottom: 10px;
        font-size: 30px;
    }

    .project-details__desc {
        font-size: 22px;
        margin-bottom: 20px;
    }

    .content-list {
        row-gap: 20px;
    }
    .content-list__item {
        font-size: 16px;
    }
}