.project {
    max-width: 370px;
    background-color: var(--poject-card-bg);
    box-shadow: var(--box-shadow);
    border-radius: 10px;
    transition: opacity 0.2s ease-in;
}

.project:hover {
    opacity: 0.7;
}

.project__img {
    border-radius: 10px;
}

.project__title {
    padding: 15px 20px 25px;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.3;
    letter-spacing: -0.5px;
    color: var(--project-card-text);
}

@media (max-width: 620px) {
        .project__title {
        font-size: 22px;
    }
}